@use 'base/variables' as v;
@use 'base/mixins' as m;
.content {
  border-radius: v.$border-radius;
  background: white;
  width: 100%;
  height: 100%;
  overflow: auto;
  scroll-behavior: smooth;
  @include m.desktop{
    padding-bottom: 100px;
  }
}
.content-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100%;
  margin: 0 50px;
  @include m.desktop{
    padding: 100px 0 0;
  } 
  @include m.telefono{
       margin: 0 3rem;
  }
}

.home h1 {
  font-size: 5.5rem;
  font-weight: bolder;
  text-align: center;
  margin: 20px;
  line-height: 1.5;
  @include m.desktop{
    font-size: 3rem;
  }
  @include m.telefono{
    font-size: 2rem;
    line-height: 2.5rem;
  }
}