*, *:before, *:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

a {
  text-decoration: none;
  color: #7c4ee6;
}

.col-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  width: 100%;
}
@media (max-width: 750px) {
  .col-2 {
    grid-template-columns: 1fr;
  }
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
@media (max-width: 1200px) {
  .container {
    height: auto;
    flex-direction: column;
  }
}

.portfolio {
  display: flex;
  background: #23a6e3;
  height: 80vh;
  width: 85vw;
  max-width: 1200px;
  border-radius: 30px;
  box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, 0.1);
}
@media (max-width: 1200px) {
  .portfolio {
    flex-direction: column;
    height: 100%;
    margin: 30px 0 100px;
  }
}

h1 {
  font-size: 4rem;
  line-height: 4rem;
}

h2 {
  font-size: 3.6rem;
  line-height: 2rem;
  font-weight: normal;
}

h3 {
  font-size: 3rem;
}

p {
  line-height: 1.5rem;
  margin-bottom: 0.7rem;
}

::-webkit-scrollbar {
  width: 8px;
}
@media (max-width: 1200px) {
  ::-webkit-scrollbar {
    width: 8px;
  }
}
@media (max-width: 750px) {
  ::-webkit-scrollbar {
    display: none;
  }
}

::-webkit-scrollbar-track {
  background: transparent;
}
@media (max-width: 1200px) {
  ::-webkit-scrollbar-track {
    background: #0a2887;
  }
}

::-webkit-scrollbar-thumb {
  background: transparent;
  border-radius: 10px;
}
@media (max-width: 1200px) {
  ::-webkit-scrollbar-thumb {
    background-color: #fff;
    border-radius: 10px;
  }
}

.content:hover::-webkit-scrollbar-thumb {
  background: rgb(194, 194, 194);
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  order: 2;
  background: #fff;
  width: 70px;
  margin-left: 20px;
  border-radius: 35px;
  padding: 15px 0;
  box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, 0.1);
}
@media (max-width: 1200px) {
  .menu {
    flex-direction: row;
    position: fixed;
    bottom: 15px;
    z-index: 1000;
    width: auto;
    height: fit-content;
    border-radius: 50px;
    margin: 0;
  }
}

.menu-icon {
  font-size: 3rem;
  color: rgb(194, 194, 194);
  margin: 1.5rem;
}
@media (max-width: 1200px) {
  .menu-icon {
    font-size: 2rem;
    margin: 0 1.5rem;
  }
}
@media (max-width: 750px) {
  .menu-icon {
    font-size: 1.5rem;
  }
}

.menu-icon:hover {
  color: #7c4ee6;
}

#navbar {
  position: fixed;
  margin: 0;
  padding: 0;
  width: 0;
  height: 0;
}

.socials {
  margin: 2rem;
  display: flex;
  flex-direction: column;
}

.socials a {
  color: #23a6e3;
  margin: 2rem;
  transition: 0.2s linear;
  transition-property: background-color, color;
  font-size: 5rem;
}

.socials span {
  font-size: 3rem;
  font-family: "Montserrat", sans-serif;
  margin: 1rem;
  font-weight: 400;
}

.socials a:hover {
  color: #7c4ee6;
  transition: 0.2s linear;
  transition-property: background-color, color;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50rem;
  height: 100%;
  border-radius: 30px;
  color: white;
  overflow: hidden;
}
@media (max-width: 1200px) {
  .header {
    width: 100%;
    height: 100%;
  }
}

.header-img {
  width: 15rem;
  height: auto;
  border-radius: 30%;
  margin: 1rem;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.5);
}
@media (max-width: 1200px) {
  .header-img {
    margin: 3rem;
  }
}

.header h1 {
  font-size: 2.5rem;
}

.header h2 {
  font-size: 2rem;
}

.header .cta {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.274);
  padding: 10px 20px;
  margin: 50px 0;
  border-radius: 30px;
  transition: 0.2s linear;
  transition-property: background-color, color;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.1);
}

.header .cta:hover {
  color: #7c4ee6;
  background-color: #fff;
  transition: 0.2s linear;
  transition-property: background-color, color;
}

.content {
  border-radius: 30px;
  background: white;
  width: 100%;
  height: 100%;
  overflow: auto;
  scroll-behavior: smooth;
}
@media (max-width: 1200px) {
  .content {
    padding-bottom: 100px;
  }
}

.content-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100%;
  margin: 0 50px;
}
@media (max-width: 1200px) {
  .content-card {
    padding: 100px 0 0;
  }
}
@media (max-width: 400px) {
  .content-card {
    margin: 0 3rem;
  }
}

.home h1 {
  font-size: 5.5rem;
  font-weight: bolder;
  text-align: center;
  margin: 20px;
  line-height: 1.5;
}
@media (max-width: 1200px) {
  .home h1 {
    font-size: 3rem;
  }
}
@media (max-width: 400px) {
  .home h1 {
    font-size: 2rem;
    line-height: 2.5rem;
  }
}

.about {
  display: flex;
  flex-direction: column;
}

.about-item {
  width: 100%;
  margin: 0.5rem 0;
}

.language {
  margin-bottom: 15px;
}

.language p {
  margin: 0;
}

.bar {
  background: rgba(0, 0, 0, 0.1);
  display: block;
  height: 8px;
  border-radius: 10px;
  overflow: hidden;
}

.bar span {
  height: 8px;
  float: left;
  background: #7c4ee6;
}

.spanish {
  width: 100%;
}

.english {
  width: 50%;
}

.skill {
  background-color: #7c4ee6;
  color: #fff;
  line-height: 35px;
  padding: 5px 10px;
  border-radius: 15px;
}

.project-list {
  margin: 20px 0;
}

.project-tile {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: end;
  background: rgb(194, 194, 194);
  color: transparent;
  width: 100%;
  height: 300px;
  margin: auto;
  overflow: hidden;
  box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.1);
}

.project-tile img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
  filter: contrast(70%) brightness(110%) grayscale(20%);
}

.overlay {
  background: rgba(168, 133, 143, 0.8784313725);
  display: flex;
  flex-direction: column;
  justify-content: end;
  width: 0%;
  height: 100%;
  color: white;
  z-index: 2;
  transition: 0.5s ease;
}

.project-description {
  line-height: normal;
  max-width: 200px;
  margin: 20px;
  position: absolute;
  left: -250px;
  transition: 0.5s ease;
}

.project-tile:hover .overlay {
  width: 100%;
  cursor: pointer;
  transition: 0.5s ease;
}

.project-tile:hover .project-description {
  left: 0;
  cursor: pointer;
  transition: 0.5s ease;
}

.coming-soon {
  font-size: 8rem;
}
@media (max-width: 750px) {
  .coming-soon {
    font-size: 5rem;
  }
}
@media (max-width: 400px) {
  .coming-soon {
    font-size: 4rem;
  }
}